import { createApp } from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
// Vuetify
import 'vuetify/styles'
import { createVuetify } from 'vuetify'
import * as components from 'vuetify/components'
import * as directives from 'vuetify/directives'

const vuetify = createVuetify({
  components,
  directives,
  theme: {
    defaultTheme: 'light',
    themes: {
      light: {
        dark: false,
        colors: {
          primary: "#ffffff",
          backcolor: "#dddddd",
          card: "#6030ff",
          textprimary: "#6030ff",
          textsecondary: "#999999",
          box: "#ffffff",
          header: "#6030ff",
        }
      },
      dark: {
        light: false,
        colors: {
          primary: "#191919",
          card: "#252525",
          backcolor: "#191919",
          textprimary: "#ffffff",
          textsecondary: "#6b6a6f",
          box: "#252525",
          header: "#BD8AFC",
        }
      },
    },
  }
})
  
createApp(App)
.use(vuetify)
.use(router)
.mount('#app')
