<script setup>
import ScaleView from '@/components/ScaleView.vue'
import ListScale from '@/js/ListScale'
//
import { ref, watch, computed } from 'vue'
//
const radio = ref(1)
const scales = ref(ListScale)
const staticScales = ref(ListScale)
const text = ref(null)

watch(text, () => {
  filterScales()
})
watch(radio, () => {
  filterScales()
})

const placeholder = computed(() => {
  return radio.value !== 1 ? 'Digite a hora sem o ":"' : 'Pesquisar pelo número da Escala'
})
//
const filterScales = () => {
  if (!text.value) {
    scales.value = staticScales.value;
  } else {
    if ( radio.value === 1 ) {
      const arr = staticScales.value.filter((el) => {
        return el.id == text.value
      });
      scales.value = arr
    } else if ( radio.value === 2 ) {
      const arr = staticScales.value.filter((el) => {
        return el.h1 == text.value || el.h2 == text.value || el.h3 == text.value || el.h4 == text.value || el.h5 == text.value || el.h6 == text.value
      });
      scales.value = arr
    } else {
      const arr = staticScales.value.filter((el) => {
        return el.h7 == text.value || el.h8 == text.value || el.h9 == text.value || el.h10 == text.value || el.h11 == text.value || el.h12 == text.value
      });
      scales.value = arr
    }
  }
}
const completeText = (t) => {
  let v = t.replace(/\D/g,'');
  v = radio.value !== 1 ? v.replace(/(\d{2})(\d)/g, "$1:$2") : v;
  v = v.toString()
  text.value = v
}
</script>

<template>
  <div class="d-flex flex-column bodys">
    <div class="d-flex flex-wrap flex-grow-0 w-100 bg-primary hei">
      <v-col
        class="d-flex align-center justify-center py-1"
        cols="12"
        sm="12"
        md="6"
      >
        <v-text-field
          v-model="text"
          :placeholder="placeholder"
          prepend-inner-icon="mdi-magnify"
          variant="outlined"
          hide-details
          rounded
          color="textprimary"
          density="compact"
          @input="(text) => completeText(text.target.value)"
        ></v-text-field>
      </v-col>
      <v-col
        class="d-flex align-center justify-center pa-1"
        cols="12"
        sm="12"
        md="6"
      >
        <v-row class="ma-0 px-1 justify-space-between align-center">
          <label class="container text-caption">Pesquisar por:</label>
          <label for="radio1" class="container text-caption pl-5">Escala
            <input type="radio" name="radio" id="radio1" @click="radio = 1" checked="checked">
            <span class="checkmark"></span>
          </label>
          <label for="radio2" class="container text-caption pl-5">H. Entrada
            <input type="radio" name="radio" id="radio2" @click="radio = 2">
            <span class="checkmark"></span>
          </label>
          <label for="radio3" class="container text-caption pl-5">H. Saída
            <input type="radio" name="radio" id="radio3" @click="radio = 3">
            <span class="checkmark"></span>
          </label>
        </v-row>
      </v-col>
      <label class="text-caption ml-2 text-textsecondary">Escalas disponíveis: {{ staticScales.length }}</label>
    </div>
    <div class="d-flex flex-column flex-grow-1 overflow-hidden">
      <div class="scroll d-flex flex-row flex-wrap pa-1 align-start justify-space-between overflow-auto bg-backcolor">
        <ScaleView
          v-for="item in scales"
          :key="item.id"
          :item="item"
        />
      </div>
    </div>
  </div>
</template>

<style scoped>
.hei {
  height: fit-content;
}

/* Customize the label (the container) */
.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 18px;
  width: 18px;
  background-color: #eee;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .checkmark {
  background-color: #BBB;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.container .checkmark:after {
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: white;
}
</style>