<script setup>
import { useRoute } from 'vue-router'
import { computed } from 'vue'
import { useTheme } from 'vuetify'
//
import SvgIcon from '@jamescoyle/vue-icon';
import { mdiArrowLeft, mdiThemeLightDark } from '@mdi/js';

const route = useRoute()
const theme = useTheme()

const disabled = computed(() => {
  return route.name !== "home" ? true : false
})
//
const toggleTheme = () => {
  theme.global.name.value = theme.global.current.value.dark ? 'light' : 'dark'
  theme.global.current.value.dark ? localStorage.setItem('brscale', JSON.stringify({dark: true})) : localStorage.setItem('brscale', JSON.stringify({dark: false}))
}

const getTheme = () => {
  const obj = JSON.parse(localStorage.getItem('brscale'))
  theme.global.name.value = obj?.dark === true ? 'dark' : 'light'
}

getTheme()
</script>

<template>
  <div class="app-bar bg-primary">
    <v-btn
      icon
      variant="plain"
      position="absolute"
      style="left: 5px;"
      size="small"
      to="/"
      v-if="disabled"
    >
      <svg-icon type="mdi" :path="mdiArrowLeft"></svg-icon>
    </v-btn>
    <h1 class="title text-h3 text-textprimary">BRSCALE</h1>
    <v-btn
      @click="toggleTheme()"
      position="absolute"
      style="right: 5px;"
      icon
      size="small"
      variant="plain"
    >
      <svg-icon type="mdi" :path="mdiThemeLightDark"></svg-icon>
      <v-tooltip activator="parent">Trocar tema</v-tooltip>
    </v-btn>
  </div>
</template>

<style scoped>

.title {
  text-align: center;
  font-family: 'brscale';
}

.app-bar {
  height: 10vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
</style>