<template>
  <div class="box bg-box border-md border-textprimary">
    <p class="bg-header text-h6 text-white font-weight-bold text-center">{{item.id}}</p>
    <span class="border-b-sm border-textprimary text-center px-5" :class="size">{{ `${item.h1} - ${item.h7}` }}</span>
    <span class="border-b-sm border-textprimary text-center px-5" :class="size">{{ `${item.h2} - ${item.h8}` }}</span>
    <span class="border-b-sm border-textprimary text-center px-5" :class="size">{{ `${item.h3} - ${item.h9}` }}</span>
    <span class="border-b-sm border-textprimary text-center px-5" :class="size">{{ `${item.h4} - ${item.h10}` }}</span>
    <span class="border-b-sm border-textprimary text-center px-5" :class="size">{{ `${item.h5} - ${item.h11}` }}</span>
    <span class="border-b-sm border-textprimary text-center px-5" :class="size">{{ `${item.h6} - ${item.h12}` }}</span>
    <span class="border-b-sm border-textprimary text-center px-5" :class="size">{{ item.type.toUpperCase() }}</span>
  </div>
</template>

<script setup>
import { useDisplay } from 'vuetify'
import { computed, defineProps } from 'vue'
//
const { name } = useDisplay()

const size = computed(() => {
  switch (name.value) {
    case 'xs': return 'text-caption'
    case 'sm': return 'text-caption'
    case 'md': return 'text-body-1'
    case 'lg': return 'text-body-1'
  }

  return undefined
})

defineProps({
  item: Object
})
</script>

<style scoped>
.box {
  width: fit-content;
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
}
</style>