<template>
  <v-dialog
    v-model="dialog"
    max-width="500"
  >
    <v-card>
      <v-card-title>{{ title }}</v-card-title>
      <v-card-subtitle>{{ subtitle }}</v-card-subtitle>
      <v-card-text>{{ text }}</v-card-text>
      <v-card-actions>
        <v-btn variant="text" class="ma-2" @click="dialog = false">FECHAR</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  data: () => {
    return {
      dialog: false,
      title: null,
      subtitle: null,
      text: null
    }
  },
  methods: {
    show(obj) {
      this.title = obj.title
      this.text = obj.text
      this.subtitle = obj.subtitle
      this.dialog = true
    }
  }
}
</script>