import moment from 'moment'

function pd(v) {
  return v < 10 ? '0' + v : v
}

function calcHoraSaida({horaEntrada, ajuste}) {
  function CountSeconds(v) {
    const r = v.split(':')
    return parseInt(r[0] * 3600) + parseInt(r[1] * 60) + parseInt(r[2])
  }
  function getHourSex(v) {
    let hours = Math.floor(v / (60 * 60))
    hours = hours >= 24 ? hours - 24 : hours
    const divisorMinutos = v % (60 * 60)
    const minutes = Math.floor(divisorMinutos / 60)
    const divisorSeconds = divisorMinutos % 60
    const seconds = Math.ceil(divisorSeconds)
    return pd(hours)+":"+pd(minutes)+":"+pd(seconds)
  }
  // lets
  const entrada = CountSeconds(moment(horaEntrada, 'HH:mm').format('HH:mm:ss')) //54000 //'15:00:00'
  const diaNormal = 30000 //'08:20:00'
  const diaAjuste = 35280 //'09:48:00'
  const iniHn = 79200//80000//80400//79200 //'22:00:00'
  const endHn = 18000 //'05:00:00'
  // const jNoturna = 28800 // 8h
  const diaTodo = 86400 // 24h
  const horaSaidaParcial = ajuste ? entrada + diaAjuste : entrada + diaNormal
  //
  //const seSaiuHn = horaSaidaParcial > iniHn && horaSaidaParcial < 104400 ? true : false
  const seEntrouHn = entrada >= iniHn || entrada < endHn ? true : false // se entrou de 22:00 às 04:59
  let horaReal
  if ( seEntrouHn ) {
    //horaReal = sumula60 ? saiuHn(horaSaidaParcial)
    //horaReal = horaSaidaParcial
    const madruga = entrada < diaTodo && entrada >= iniHn ? false : true
    const diffProdia = horaSaidaParcial - endHn
    let diffMadruga  = (endHn - entrada)
    if ( madruga ) {
      horaReal = (diffMadruga * 0.875) + diffProdia
    } else {
      diffMadruga = ((diaTodo - (entrada - endHn) * 0.875) + diffProdia) - endHn
      horaReal = diffMadruga
    }
  } else {
    if ( horaSaidaParcial < iniHn ) {
      horaReal = horaSaidaParcial
    } else {
      const diff = (horaSaidaParcial - iniHn)
      const reducao = diff * 0.875
      horaReal  = (horaSaidaParcial - diff) + reducao
    }
  }

  return moment(getHourSex(horaReal), 'HH:mm:ss').format('HH:mm')
}

export default calcHoraSaida;