<template>
  <v-row class="ma-0 bodys">
    <dialog-view
      ref="dialog"
    />
    <div class="d-flex flex-column flex-sm-row position-relative w-100 justify-center align-center bg-backcolor">
      <v-btn
        icon
        class="ma-2"
        color="textsecondary"
        variant="text"
        style="position: absolute;right: 5px;top: 5px"
        @click="dialog.show({
          title: 'Informações sobre esta tela',
          subtitle: 'compensando?',
          text: 'Esta tela foi criada pensando numa jornada de 07:20. O horário compensado representa um acréscimo de 01:28. Dessa forma, uma jornada compensando é de 08:48.'
        })"
      >
        <svg-icon type="mdi" :path="mdiHelpCircle"></svg-icon>
      </v-btn>
      <div class="d-flex flex-column align-center justify-center ma-2">
        <p class="text-h5 text-center">Escolha o horário inicial</p>
        <v-dialog
          v-model="modal"
          width="auto"
        >
          <template v-slot:activator="{ props: activatorProps }">
            <input
              id="input"
              v-model="time"
              :active="modal"
              :focused="modal"
              class="text-center text-h3 text-textprimary"
              readonly
              v-bind="activatorProps"
              size="5"
            />
          </template>
          <template v-slot:default>
            <v-time-picker
              v-if="modal"
              v-model="time"
              format="24hr"
              color="#6030ff"
              v-on:update:minute="modal = false"
            ></v-time-picker>
          </template>
        </v-dialog>
        <v-switch label="Compensando?" v-model="comp" color="textprimary"></v-switch>
      </div>
      <div class="d-flex flex-column rounded-lg pa-2 bg-card">
          <p class="text-center text-white font-weight-bold">Resultado</p>
          <p class="text-center text-white mt-3">Segundo a escala</p>
          <p class="text-center text-white text-h3 result1">{{ result1 }}
            <v-btn
              icon
              class="ma-2"
              color="textsecondary"
              variant="text"
              style="position: absolute;right: -50%;top: 0"
              @click="dialog.show({
                title: 'Segundo a escala',
                text: 'Aqui é mostrado o resultado segundo as escalas disponíveis. Ou seja, o app faz uma varredura nas escalas disponíveis e compara um horário de saída que seja compatível com o horário informado pelo usuário.'
              })"
            >
              <svg-icon type="mdi" :path="mdiHelpCircle"></svg-icon>
            </v-btn>
          </p>

          <p class="text-center text-white mt-3">Cálculo livre</p>
          <p class="text-center text-white text-h4 result2">{{ result2 }}
            <v-btn
              class="ma-2"
              color="textsecondary"
              icon
              variant="text"
              style="position: absolute;right: -50%;top: 0"
              @click="dialog.show({
                title: 'Cálculo livre',
                text: 'Quando o app não encontra nenhum horário nas escalas disponíveis compatível com o horário informado pelo usuário, ele tenta fazer um cálculo por si só baseado na informação passada pelo usuário. Alertamos que esse cálculo pode falhar e não deve ser usado para basear seus horários no local de trabalho. Qualquer dúvida, procure seus gestores ou RH.'
              })"
            >
              <svg-icon type="mdi" :path="mdiHelpCircle"></svg-icon>
            </v-btn>
          </p>
      </div>
    </div>
  </v-row>
</template>

<script setup>
  import ListScale from '@/js/ListScale.js'
  import calcHoraSaida from '@/js/functions.js'
  import { ref, watch } from 'vue'
  import DialogView from '@/components/DialogView.vue'
  import { VTimePicker } from 'vuetify/lib/labs/components.mjs'
  //
  import SvgIcon from '@jamescoyle/vue-icon';
  import { mdiHelpCircle } from '@mdi/js';

  const time = ref("07:00")
  const modal = ref(false)
  const comp = ref(false)
  const result1 = ref("15:20")
  const result2 = ref("15:20")
  const dialog = ref()
  const scales = ListScale
  const calchorasaida = calcHoraSaida

  watch(time, () => {
    filterResult()
  })
  watch(comp, () => {
    filterResult()
  })

  const filterResult = () => {
    const arr = []
    const aj = comp.value ? 'ajuste' : 'normal'
    scales.forEach(item => {
      if(item.h1 === time.value && item.type == aj){arr.push(item.h1, item.h7)}
      if(item.h2 === time.value && item.type == aj){arr.push(item.h2, item.h8)}
      if(item.h3 === time.value && item.type == aj){arr.push(item.h3, item.h9)}
      if(item.h4 === time.value && item.type == aj){arr.push(item.h4, item.h10)}
      if(item.h5 === time.value && item.type == aj){arr.push(item.h5, item.h11)}
      if(item.h6 === time.value && item.type == aj){arr.push(item.h6, item.h12)}
    })
    const newArr = arr.filter((este, i) => arr.indexOf(este) === i);
    result1.value = newArr.length > 0 ? newArr.at(-1) : '--:--'
    if ( result1.value == "--:--" ) {
      freeCalc()
    } else {
      result2.value = "--:--"
    }
  }

  const freeCalc = () => {
    result2.value = calchorasaida({ horaEntrada: time.value, ajuste: comp.value })
  }
</script>

<style scoped>
.result1, .result2 {
  position: relative;
}

#input {
  outline: none;
  border: none;
}
</style>