<script setup>
import { Vue3Lottie } from 'vue3-lottie'
import { useDisplay } from 'vuetify'
import { computed, defineProps } from 'vue'

defineProps({
  title: String,
  text: String,
  link: String,
  lottie: Object
})

const { name } = useDisplay()

const height = computed(() => {
  switch (name.value) {
    case 'xs': return 200
    case 'sm': return 200
    case 'md': return 300
    case 'lg': return 300
    case 'xl': return 400
    case 'xxl': return 400
  }

  return undefined
})

const letterSize = computed(() => {
  switch (name.value) {
    case 'xs': return 'text-body-1'
    case 'sm': return 'text-body-1'
    case 'md': return 'text-h6'
    case 'lg': return 'text-h4'
  }

  return undefined
})

const titleSize = computed(() => {
  switch (name.value) {
    case 'xs': return 'text-h5'
    case 'sm': return 'text-h5'
    case 'md': return 'text-h3'
    case 'lg': return 'text-h3'
  }

  return undefined
})
</script>

<template>
  <v-card class="d-flex flex-column rounded-lg h-100 bg-card">
    <v-card-title class="text-white text-left" :class="titleSize">{{title}}</v-card-title>
    <v-row class="flex-row flex-grow-1 ma-0 pa-1">
      <v-col class="pa-0" cols="6">
        <Vue3Lottie :animationData="lottie" :height="height" :width="height" />
      </v-col>
      <v-col class="pa-0 pa-md-1" cols="6">
        <p class="pa-0 ma-0 text-white text-center" :class="letterSize">{{ text }}</p>
      </v-col>
    </v-row>
    <v-row class="ma-0 pb-2 pr-2 flex-grow-0 justify-end align-center">
      <v-btn
        variant="tonal"
        color="white"
        size="x-large"
        rounded="lg"
        :to="link"
      >
        VER
      </v-btn>
    </v-row>
  </v-card>
</template>