<script setup>
import { ref } from "vue"
import SvgIcon from '@jamescoyle/vue-icon'
import { mdiApps, mdiHelpCircle } from '@mdi/js'
import axios from 'axios'
//
import ProdInfo from '@/components/ProdInfo.vue'
import InfoView from '@/components/InfoView.vue'
//
const prodinfo = ref()
const info = ref()
const loading = ref(false)
const json = ref([])

const getJson = async () => {
  loading.value = true
  await axios
    .get("https://cdn.statically.io/gh/blbsouza/cdn/main/json/web-app.json")
  .then(r => json.value = r.data)
  .finally(() => loading.value = false)
}

getJson()
</script>

<template>
  <ProdInfo ref="prodinfo" />
  <InfoView ref="info" :json="json" />
  <div class="footer d-flex flex-row align-center justify-space-between pa-1 bg-primary">
    <div class="d-flex flex-row flex-wrap">
      <p class="text-caption text-textsecondary">brscale 2.1, blbtech &copy; all rights reserved</p>
    </div>
    <div class="d-flex flex-row">
      <v-btn
        color="textsecondary"
        icon
        variant="text"
        :loading="loading"
        @click="prodinfo.open({json: json})"
      >
        <svg-icon type="mdi" :path="mdiApps"></svg-icon>
      </v-btn>
      <v-btn
        color="textsecondary"
        icon
        variant="text"
        :loading="loading"
        @click="info.open()"
      >
        <svg-icon type="mdi" :path="mdiHelpCircle"></svg-icon>
      </v-btn>
    </div>
  </div>
</template>

<style scoped>
.footer {
  height: 7%;
  width: 100%;
  overflow: hidden;
}

.footer-center {
  max-width: 30%;
  overflow: hidden;
}
</style>